<template>
     <div style="background-image:url('/static/assets/img/login.jpg')">
       <div  class="login-page" style="padding-bottom:300px">
      <center >
        <img src="/static/assets/img/logo.png" height="100" alt="">
      </center>
  <div class="form">
     
    <form class="login-form">
     <div >
            <input type="email" id="inputemail" v-validate="'required'" v-model="userObj.email" name="email" placeholder="Email">
            <span style="color:red"><small>{{ errors.first('email') }}</small></span>
          </div>
          <div>
            <input type="password" v-model="userObj.password" v-validate="'required'" id="inputPassword" name="password"
              placeholder="Password">
            <span style="color:red"><small>{{ errors.first('password') }}</small></span>
          </div>
          <template>
  <v-progress-linear color="primary" :indeterminate="true" v-show='this.routeStore.loading' ></v-progress-linear>
</template>
      <button type="submit" @click.prevent="loginUser()" v-show='!this.routeStore.loading'>login</button>
      <button type="submit" v-show='this.routeStore.loading'>Loading...</button>
    </form>
  </div>
</div>
         

     </div>
     </div>
</template>
<script>
import {loginMixin} from '@/mixins'
import {mapState} from 'vuex'
    export default{
      data(){
        return {
        }
      },
      mixins:[loginMixin],
      
     
    }
</script>
 <style scoped>
 

.login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
}
.form {
  position: relative;
  z-index: 1;
  background-color: rgba(	48,89,102,0.5);
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: black;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,.form button:active,.form button:focus {
  background: black;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form .message a {
  color: #4CAF50;
  text-decoration: none;
}
.form .register-form {
  display: none;
}
.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container:before, .container:after {
  content: "";
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #EF3B3A;
}

 </style>



